import React, {useEffect,useState} from 'react';
import {
Typography,
Avatar,
Grid,
Box,
IconButton,
Divider,
Paper
} from "@material-ui/core";
/**import avatar from "../images/profilepic.png"; **/
import Typed from "react-typed";
import {makeStyles} from "@material-ui/core/styles";
import {motion} from "framer-motion";
import {Link} from "react-scroll";
import arrow from "../images/arrow.png";
import background from "../images/background.jpg";
import Particles from "react-particles-js";
import concordiaImg from "../images/concordia-university.jpg"
import Aos from "aos";
import "aos/dist/aos.css";
import Contacts from "./Contacts";

// CSS STYLES
const useStyles = makeStyles(theme=> ({
    title: {
        position: "absolute",
        transform: "translate(-50%, -50%)", 
        top: "35%",
        left: "22%",
        color: "black",
        fontWeight: "500",
        fontSize: "1.5em",
        zIndex: -1
    },
    container: {
        position: "relative",
        width: "100%",
        height: "100vh",
        textAlign: "center",
        background: "url("+ background +")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "contain",
        backgroundSize: "cover",
        backgroundPosition: "center",
        zIndex: 1
    },
    blackDivider: {
        width: "100%",
        height: "0.5rem",
        backgroundColor: "black"
    },
    particlesCanvas: {
        position: "absolute",
        opacity: "0.9",
        right: "0px"     
    },
    about: {
        width: "100%",
        backgroundColor: "white",
        color: "black",
        textAlign: "center",
        fontSize: "1.5em",
        paddingTop: "3.5%",
        paddingBottom: "2.5%"     
    },
    rootGrid: {
        width: "100%",
        margin: "0px"
      },
    gridLayout: {
        backgroundColor: "white",
        border: "2px solid black",
        borderRadius: "5px",
        padding: theme.spacing(15),
        textAlign: "center"
        
    }
}));

const Home = () => {
    const classes = useStyles();
    useEffect(() => {
        Aos.init({ 
            offset: 120,
            duration: 500,
            delay: 0
        });
    }, []);

    return (
        <>
        <Box className={classes.container} component="div" id="home">
        <Particles canvasClassName={classes.particlesCanvas}
            params={{
                "particles": {
                    line_linked: {
                        "color": "#f57c00"
                    },
                    
                    "color": {
                        "value": "#ef5350"
                    },
                    "number": {
                        "value": 30
                    },
                    "size": {
                        "value": 8
                    }
                },
                "interactivity": {
                    "events": {
                        "onhover": {
                            "enable": true,
                            "mode": "repulse"
                        }
                    }
                }
            }} /> 
            <Typography className={classes.title}>
                <Typed 
                  strings={[""]} 
                  typeSpeed={40}
                  backSpeed={30}
                  loop
                />
            </Typography>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
           
            <IconButton 
                component={Link} to="about" smooth={true} duration={1000} style={{position: "absolute", top:"80%", left:"50%", transform: "translateX(-50%)"}}
            >
             <img src={arrow} style={{width: '40px', height: '40px'}}></img> 
            </IconButton>
         
        </Box>
        <Box component="div" id="about" className={classes.blackDivider}>
        </Box>
        <Box style={{overflowX:"hidden"}}>
          <Typography data-aos= "fade-left" className={classes.about}>
                About Me <tr></tr>
          </Typography>
        </Box>
        <div style={{width: "100%", height: "1000px", background: "white"}}>
            <div style={{marginLeft:"20px", marginRight: "20px", overflowX: "hidden"}}>
                <Grid container spacing={3} className={classes.rootGrid}>  
                    <Grid item xs={12} md={6}>
                        <Paper data-aos="fade-right" className={classes.gridLayout}>xs=6</Paper>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Paper data-aos="fade-left" className={classes.gridLayout}>MSMYE IS</Paper>
                    </Grid>
                    <Grid item xs={12} md={12}>
                        <Paper data-aos="fade-right" className={classes.gridLayout}>MSMYE IS</Paper>
                    </Grid>
                </Grid>
            </div>
        </div>
        <div id="contact">
          <Contacts/>
        </div>
        </>
    )
}

export default Home
